.header {
  z-index: 100;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  transition-duration: .5s;
  padding: 10px 0;
  background-color: #1C1E53;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 56px;
    gap: 20px;
  }

  &__logo {
    margin-right: 20px;
    z-index: 10;
    justify-self: center;
  }

  &__buttons {
    display: flex;
    gap: 30px;
    align-items: center;

    .button {
      border: 2px solid #f4f6fc4b;
      color: #fff;
      font-size: 16px;
      font-family: "Poppins", sans-serif;
      font-weight: 500;
      background-color: transparent;
      padding: 16px 48px;
      border-radius: 40px;
      transition-duration: .5s;
      position: relative;
      overflow: hidden;
      cursor: pointer;
      text-decoration: none;

      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: -250%;
        width: 200%;
        height: 100%;
        background: linear-gradient(120deg,
            rgba(255, 255, 255, 0) 0%,
            rgba(255, 255, 255, 0.35) 50%,
            rgba(255, 255, 255, 0) 100%);
        transform: skew(45deg);
        transition: left 0.8s ease;
      }

      &:hover::before {
        left: 150%;
      }

      &:hover {
        opacity: .5;
      }

      @media (max-width: 992px) {
        font-size: 20px;
      }
    }

    @media (max-width: 992px) {
      flex-direction: column;

      button {
        width: 100%;
      }
    }
  }

  &__nav {
    width: 100%;
    max-width: 500px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 15px;

    .header__link {
      color: #BBBBCB;
      font-size: 16px;
      font-weight: 700;
      line-height: 26px;
      width: fit-content;
      position: relative;
      text-decoration: none;

      @media (max-width: 992px) {
        font-size: 20px;
      }

      &::after {
        content: '';
        width: 0;
        height: 1px;
        background-color: #BBBBCB;
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        transition-duration: .5s;
      }

      &:hover {
        &::after {
          width: 100%;
        }
      }

      &.active {
        &::after {
          width: 100%;
        }
      }
    }
  }

  @media (max-width: 992px) {
    &__nav {
      position: absolute;
      right: -100%;
      top: 0;
      flex-direction: column;
      gap: 25px;
      padding: 150px 0 50px 0;
      align-items: center;
      justify-content: flex-start;
      width: 100%;
      height: 100vh;
      min-height: 100vh;
      background-color: #1C1E53;
      transition-duration: .5s;
      overflow-y: auto;

      &.active {
        right: 0;
      }
    }

    &__burger {
      min-width: 20px;
      width: 40px;
      height: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      position: relative;
      z-index: 11;

      span {
        display: block;
        width: 100%;
        height: 2px;
        background-color: #fff;
        position: relative;
        transition-duration: .5s;

        &::before,
        &::after {
          content: "";
          position: absolute;
          width: 100%;
          height: 2px;
          background-color: #fff;
          transition-duration: .5s;
        }

        &::before {
          top: -10px;
        }

        &::after {
          top: 10px;
        }
      }

      &.active {
        span {
          background-color: transparent;

          &::before {
            top: 0;
            transform: rotate(45deg);
          }

          &::after {
            top: 0;
            transform: rotate(-45deg);
          }
        }
      }
    }
  }
}